.App {
  text-align: center;
}


.App-header {
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: black;
  min-height: 80px;
  width: 100%;
  z-index: 15;
  padding: 0px 45px;
  position: absolute;
}

.App-link {
  color: #61dafb;
}

.content-body{
  display: flex;
  height: 100vh;
}

.bg{
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  /* filter: grayscale(10); */
}
